@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
.color {
    min-width: 4px;
    transition: .3s all;
}
.color.detach {
    cursor: pointer;
    transform: scaleX(1.5);
    border: 1px solid white;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.384);
    padding: 3px;
    transition: .3s all;
}
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
body {
  background: #f6fafb;
  overflow: hidden;
}
.colors{
  height: 15vh;
  width: 100%;
  max-width: 100%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.panel{
    height: 90vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    color: white;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
    font-weight: 300;
    font-size: 30pt;
    max-width: 100%;
    min-width: 100%;
    width: 100%;
}

.panel div{
  display: block;
  margin: 25px;
  transition: .3s all;
}
.panel div.blackfont{
  color: black !important;
  transition: .3s all;
}

.panel div svg {
  position: relative;
  display: block;
  border: 0px;
  outline:none;
  float: right;
  margin-left: 7px;
  color: white;
  font-size: 25pt;
  background: transparent;
  cursor: pointer;
  transition: .3s all;
  z-index: 99;
}
.panel div svg:hover{
  color: rgba(0, 0, 0, 0.164);
  transition: .3s all;
}
.panel div svg.blackfont{
  color: black !important;
}

.panel div svg.blackfont:hover{
  color: rgba(255, 255, 255, 0.582) !important;
}

.codes div{
  position: relative;
}
.msg {
  background: transparent;
  text-align: center;
  opacity: 0;
  text-transform: capitalize;
  font-size: 13pt;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  position: absolute !important;
  right: -55px;
  top: 4px;
  transition: .3s all;
}
.msg.effect{
  opacity: 1;
  transition: .3s all;
}

section.config {
  font-size: 12pt;
  display: flex;
}

section.config .title{
  font-size: 14pt;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;  
  background: white;
  outline: none;
  opacity: 1;
}

.slider:hover::-webkit-slider-thumb{
  background: rgb(0, 0, 0);
  -webkit-transition: .3s all;
  transition: .3s all;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%; 
  background: #ffffff;
  border: 1px solid black;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4CAF50;
  cursor: pointer;
}
@media screen and (max-width: 500px){
    .panel div svg{
        display: none;
    }
}

@media screen and (max-width: 400px){
    .panel{
        font-size: 21pt;
    }
    .panel div{
        margin: 14px;
    }
}
